import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormControl, FormGroupDirective, FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { first } from 'rxjs/operators';

import { VacationService, AccountService, AlertService } from '../_services';

import { VacationType } from '../_models/vacation';

@Component({
  selector: 'app-add-edit',
  templateUrl: './add-edit.component.html',
  styleUrls: ['./add-edit.component.scss']
})
export class AddEditComponent implements OnInit {
    form: FormGroup;
    id: string;
    isAddMode: boolean;
    loading = false;
    submitted = false;
    VacationType = VacationType;

    constructor(
        public formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private router: Router,
        private vacationService: VacationService,
        private accountService: AccountService,
        private alertService: AlertService
    ) {}

    ngOnInit() {
        this.id = this.route.snapshot.params['id'];
        this.isAddMode = !this.id;

        // // password not required in edit mode
        // const passwordValidators = [Validators.minLength(6)];
        // if (this.isAddMode) {
        //     passwordValidators.push(Validators.required);
        // }

        this.form = this.formBuilder.group({
            title: ['', Validators.required],
            description: ['', Validators.required],
            type: [null, Validators.required]
        });

        if (!this.isAddMode) {
            // this.accountService.getById(this.id)
            //     .pipe(first())
            //     .subscribe(x => {
            //         this.f.firstName.setValue('');
            //         this.f.lastName.setValue('');
            //         this.f.username.setValue(x.username);
            //     });
            this.vacationService.getAll()
                .pipe(first())
                .subscribe(vacs => {
                    const vac = vacs.find(v => v.id === this.id);
                    this.f.title.setValue(vac.title);
                    this.f.description.setValue(vac.description);
                    this.f.type.setValue(vac.type);
                });
        }
    }

    // convenience getter for easy access to form fields
    get f() { return this.form.controls; }

    onSubmit() {
        this.submitted = true;

        // reset alerts on submit
        this.alertService.clear();

        // stop here if form is invalid
        if (this.form.invalid) {
            return;
        }

        this.loading = true;
        if (this.isAddMode) {
            this.createVacation();
        } else {
            //this.updateUser();
            console.log('should edit!!!');
        }
    }

    private createVacation() {
      const newVac = this.form.value;
      newVac.id = this.accountService.userValue.id + '-' + this.uuidv4();
      console.log('create vac !!!', newVac);
        this.vacationService.createVacation(newVac)
            .pipe(first())
            .subscribe(
                data => {
                    this.alertService.success('Vacation added successfully', { keepAfterRouteChange: true });
                    this.router.navigate(['.', { relativeTo: this.route }]);
                },
                error => {
                    this.alertService.error(error);
                    this.loading = false;
                });
    }

    private uuidv4() {
      return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        const r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
      });
    }

    // private updateUser() {
    //     this.accountService.update(this.id, this.form.value)
    //         .pipe(first())
    //         .subscribe(
    //             data => {
    //                 this.alertService.success('Update successful', { keepAfterRouteChange: true });
    //                 this.router.navigate(['..', { relativeTo: this.route }]);
    //             },
    //             error => {
    //                 this.alertService.error(error);
    //                 this.loading = false;
    //             });
    // }
}
